<template>
    <div class="enter">
        <div class="imgs"><img src="https://assets.maizuo.com/h5/mz-auth/public/app/img/logo.19ca0be.png" alt=""></div>
        <div class="sjh"><input id="setphone" type="text" placeholder="手机号" maxlength="11"><span>获取验证码</span></div>
        <div class="yzm"><input id="setyzm" type="text" placeholder="密码"></div>
        <div class="dl"><button @click="setUser()">注册</button></div>
    </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
export default {
    data(){
        return {
            err:false
        }
    },
    methods:{
        setUser(){
            // console.log(111)
            let phones = document.getElementById("setphone").value
            let passwords = document.getElementById("setyzm").value
            // console.log(phones,passwords)
            if(!(phones == "" || passwords == "")){
                if(/^1[3456789]\d{9}$/.test(phones)){
                    let param = new URLSearchParams()
                    param.append('phone', phones)
                    param.append('password', passwords)
                    this.$http({
                        method: 'post',
                        url: '/api/mz/users/to',
                        data: param
                    }).then(res=>{
                        MessageBox("提示",res.data.result)
                        if(res.data.result == "注册成功"){
                            this.$router.push("/users")
                        }
                    })
                }else{
                    MessageBox("请填写手机号")
                }
            }else{
                MessageBox("不能为空")
            }

        }
    }

}
</script>

<style lang="scss">
.enter{
    padding-top:80px;
    .imgs{
        width: 100%;
        height:60px;
        margin-bottom: 40px;
        text-align: center;
        img{
            height:60px;
            display: inline-block;
        }
    }
    .sjh{
        margin:0 25px;
        overflow: hidden;
        border-bottom: 1px solid #ededed;
        input{
            padding:20px 0;
            height:55px;
            width:235px;
            float: left;
            background: #fff;
            border:none;
        }
        span{
            float: left;
            height: 55px;
            width:90px;
            line-height:55px;
        }
    }
    .yzm{
        margin:0 25px;
        border-bottom: 1px solid #ededed;
        input{
            padding:20px 0;
            height:55px;
            width:325px;
            background:#fff;
            border:none;
        }
    }
    .dl{
        width:100%;
        height:225px;
        padding:70px 25px 0 25px;
        button{
            width:100%;
            height:44px;
            background: #ff5f16;
            border:none;
            border-radius: 5px;
            font-size: 16px;
            color:#fff;
        }
    }
}
</style>